            </div>
        </div>
        <div style="text-align:end;">
            <span class=" flex-col sm:flex-row sm:items-end xl:items-start" style="text-align: end;"
                v-if="this.isPass == true">
                <button class="btn btn-primary w-1/2 sm:w-auto mr-2 mb-3" @click="uploadData()" style="width: auto;">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
                        stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"
                        class="feather feather-file-text hidden sm:block w-4 h-4 mr-2">
                        <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z"></path>
                        <polyline points="14 2 14 8 20 8"></polyline>
                        <line x1="16" y1="13" x2="8" y2="13"></line>
                        <line x1="16" y1="17" x2="8" y2="17"></line>
                        <polyline points="10 9 9 9 8 9"></polyline>
                    </svg>
                    บันทึกข้อมูล </button>
            </span>
            <span class=" flex-col sm:flex-row sm:items-end xl:items-start" style="text-align: end;"
                v-if="this.isPass == false">
                <button class="btn btn-danger w-1/2 sm:w-auto mr-2 mb-3" disabled="" style="width: auto;">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
                        stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"
                        class="feather feather-file-text hidden sm:block w-4 h-4 mr-2">
                        <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z"></path>
                        <polyline points="14 2 14 8 20 8"></polyline>
                        <line x1="16" y1="13" x2="8" y2="13"></line>
                        <line x1="16" y1="17" x2="8" y2="17"></line>
                        <polyline points="10 9 9 9 8 9"></polyline>
                    </svg>
                    บันทึกข้อมูล </button>
            </span>
            <span class=" flex-col sm:flex-row sm:items-end xl:items-start" style="text-align: end;">
                <button class="btn btn-linkedin w-1/2 sm:w-auto mr-2 mb-3" @click="exportData()" style="width: auto;">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
                        stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"
                        class="feather feather-file-text hidden sm:block w-4 h-4 mr-2">
                        <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z"></path>
                        <polyline points="14 2 14 8 20 8"></polyline>
                        <line x1="16" y1="13" x2="8" y2="13"></line>
                        <line x1="16" y1="17" x2="8" y2="17"></line>
                        <polyline points="10 9 9 9 8 9"></polyline>
                    </svg>
                    Download Excel </button>
            </span>
        </div>
        <span>
            <label style="font-size:24px;">Affiliate Spec</label>
        </span>
        <div class="mt-1">
            <label style="font-size:18px;"> อ่านข้อมูลจาก sheet AffSpec เท่านั้น </label>
        </div><br>
        <input type="file" class="form-control mb-4" style="width:50%"
            accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
            @change="handleFileUpload($event)" ref="fileInput">

        <span style="margin-left:1rem;">
            <span class=" flex-col sm:flex-row sm:items-end xl:items-start" style="text-align: end;">
                <button class="btn btn-danger w-1/2 sm:w-auto mr-2 mb-3" @click="removeFiles()" style="width: auto;">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
                        stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"
                        class="feather feather-trash-2 w-4 h-4 mr-1">
                        <polyline points="3 6 5 6 21 6"></polyline>
                        <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
                        <line x1="10" y1="11" x2="10" y2="17"></line>
                        <line x1="14" y1="11" x2="14" y2="17"></line>
                    </svg>
                    ลบ
                </button>
            </span>
            <span class=" flex-col sm:flex-row sm:items-end xl:items-start" style="text-align: end;"
                v-if="this.doing == false">
                <button class="btn btn-linkedin w-1/2 sm:w-auto mr-2 mb-3" @click="checkFiles()" style="width: auto;">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
                        stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"
                        class="feather feather-check-square w-4 h-4 mr-1">
                        <polyline points="9 11 12 14 22 4"></polyline>
                        <path d="M21 12v7a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h11"></path>
                    </svg>
                    ตรวจสอบ
                </button>
            </span>
        </span>
        <div class="mb-1 mt-3">
            <label v-if="this.fileValue == false" style="font-size:24px;">Data Preview</label>
        </div>
        <div id="chats" class="tab-pane active mr-1 ml-1" role="tabpanel" aria-labelledby="chats-tab">
            <div class="overflow-x-auto scrollbar-hidden">
                <div class="overflow-x-auto">
                    <table class="table mt-3" id="table_id1" v-if="this.fileValue == false">
                        <thead style="back">
                            <tr class="bg-gray-200   text-center">
                                <th class="whitespace-nowrap" style="text-align:center;">Check Correct</th>
                                <th class="whitespace-nowrap" style="text-align:center;">Message/ข้อความ</th>
                                
                                <th class="whitespace-nowrap" style="text-align:center;">Guid</th>
                                <th class="whitespace-nowrap" style="text-align:center;">MinAmt</th>
                                <th class="whitespace-nowrap" style="text-align:center;">Type</th>
                                <th class="whitespace-nowrap" style="text-align:center;">KeyGuid</th>
                                <th class="whitespace-nowrap" style="text-align:center;">HcFrom</th>
                                <th class="whitespace-nowrap" style="text-align:center;">HcTo</th>
                                <th class="whitespace-nowrap" style="text-align:center;">EventType</th>
                                <th class="whitespace-nowrap" style="text-align:center;">Remark</th>
                            </tr>
                        </thead>

                    </table>

                    <table class="table mt-3" id="table_id" v-else>
                        <thead style="back">
                            <tr class="bg-gray-200 text-center">
                                <th class="whitespace-nowrap" style="text-align:center;">Check Correct</th>
                                <th class="whitespace-nowrap" style="text-align:center;">Message/ข้อความ</th>

                                <th class="whitespace-nowrap" style="text-align:center;">Guid</th>
                                <th class="whitespace-nowrap" style="text-align:center;">MinAmt</th>
                                <th class="whitespace-nowrap" style="text-align:center;">Type</th>
                                <th class="whitespace-nowrap" style="text-align:center;">KeyGuid</th>
                                <th class="whitespace-nowrap" style="text-align:center;">HcFrom</th>
                                <th class="whitespace-nowrap" style="text-align:center;">HcTo</th>
                                <th class="whitespace-nowrap" style="text-align:center;">EventType</th>
                                <th class="whitespace-nowrap" style="text-align:center;">Remark</th>
                            </tr>
                        </thead>
                        <tbody class=" text-center ">
                            <tr v-for="ar in allrec" :key="ar.id">

                                <td class="border-b dark:border-dark-5" v-if="ar.isPass == true">{{ ar.isPass }}</td>
                                <td class="border-b dark:border-dark-5" v-if="ar.isPass == false" style="color:red;">
                                    {{ ar.isPass }}</td>
                                <td class="border-b dark:border-dark-5" style="color:red; white-space: nowrap;">
                                    {{ ar.uploadMsg }}</td>
                                    
                                <td class="border-b dark:border-dark-5" style="white-space: nowrap;">{{ ar.affSpecGuid }}</td>
                                <td class="border-b dark:border-dark-5" style="white-space: nowrap;">{{ ar.affSpecMinAmt }}</td>
                                <td class="border-b dark:border-dark-5" style="white-space: nowrap;">{{ ar.affSpecType }}</td>
                                <td class="border-b dark:border-dark-5" style="white-space: nowrap;">{{ ar.affSpecKeyGuid }}</td>
                                <td class="border-b dark:border-dark-5" style="white-space: nowrap;">{{ ar.affSpecHcFrom }}</td>
                                <td class="border-b dark:border-dark-5" style="white-space: nowrap;">{{ ar.affSpecHcTo }}</td>
                                <td class="border-b dark:border-dark-5" style="white-space: nowrap;">{{ ar.affSpecEventType }}</td>
                                <td class="border-b dark:border-dark-5" style="white-space: nowrap;">{{ ar.affSpecRemark }}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>

</template>
<script>
import ImportService from '@/services/import.service';
import moment from 'moment'
import axios from "axios";
import { useToast } from "vue-toastification";
export default {
    name: 'AffiliateSpecImport',
    data() {
        return {
            toast: useToast(),
            allrec: [],
            fileValue: false,
            isPass: false,
            doing: false,
        }
    },
    props: {
    },
    methods: {
        handleFileUpload() {
            var files = {};
            files = this.$refs.fileInput.files[0];
            let input = this.$refs.fileInput;
            let file = input.files;
            this.fileName = file[0].name;
            this.file = files;
            this.Name = files.name;
            this.fileValue = true;
        },
        removeFiles() {
            this.fileValue = false;
            this.$refs.fileInput.value = null;
            this.file = null;
            this.Name = null;
            this.isPass = false;
            this.doing = false;
            this.allrec = [];
            var table = $('#table_id').DataTable();
            table

            table.destroy();
        },
        uploadData() {
            ImportService.uploadFileAffSpec(this.allrec).then((data) => {
                if (data.status == true) {
                    this.$store.dispatch('main/changeLoading', false);
                    this.toast.success("บันทึกสำเร็จ", { timeout: 2000 });
                    this.$router.push("/affiliate-spec-search");
                }
                else {
                    this.$store.dispatch('main/changeLoading', false);
                    this.toast.error(data.data, { timeout: 2000 });
                }
            });
        },
        checkFiles() {
            this.$store.dispatch('main/changeLoading', true);
            let formData = new FormData();
            formData.append('file', this.file);
            ImportService.checkFileAffSpec(formData).then((data) => {
                if (data.status == true) {
                    if (data.data.lstData.length != 0) {
                        this.allrec = data.data.lstData;
                        this.isPass = data.data.isPass;
                        this.doing = true;
                        $.extend(true, $.fn.dataTable.defaults, {
                            "language": {
                                "sProcessing": "กำลังดำเนินการ...",
                                "sLengthMenu": "แสดง _MENU_ แถว",
                                "sZeroRecords": "ไม่พบข้อมูล",
                                "sInfo": "แสดง _START_ ถึง _END_ จาก _TOTAL_ แถว",
                                "sInfoEmpty": "แสดง 0 ถึง 0 จาก 0 แถว",
                                "sInfoFiltered": "(กรองข้อมูล _MAX_ ทุกแถว)",
                                "sInfoPostFix": "",
                                "sSearch": "ค้นหา:",
                                "sUrl": "",
                                "oPaginate": {
                                    "sFirst": "เริ่มต้น",
                                    "sPrevious": "ก่อนหน้า",
                                    "sNext": "ถัดไป",
                                    "sLast": "สุดท้าย"
                                }
                            }
                        });
                        $(document).ready(function () {
                            $('#table_id').DataTable({
                            });
                        });
                        this.$store.dispatch('main/changeLoading', false);
                    } else {
                        this.removeFiles();
                        this.$store.dispatch('main/changeLoading', false);
                        this.toast.error("ไม่พบข้อมูลใน Sheet AffSpec", { timeout: 2000 });

                    }

                }
                else {
                    this.$store.dispatch('main/changeLoading', false);
                    this.toast.error(data.data, { timeout: 2000 });
                }

            });
        },

        exportData() {
            axios({
                url: process.env.VUE_APP_BASE_API + '/MasterData/AffSpec_Download', // File URL Goes Here
                method: 'GET',
                responseType: 'blob',
            }).then((response) => {
                var blob = new Blob([response.data]);
                var downloadElement = document.createElement("a");
                var href = window.URL.createObjectURL(blob); //create the download url
                downloadElement.href = href;
                downloadElement.download = "AffiliateSpec" + moment(new Date()).format('_DDMMYYYY_hhmmss') + ".xlsx"; //the name of the downloaded file
                document.body.appendChild(downloadElement);
                downloadElement.click(); //click to file
                document.body.removeChild(downloadElement); //remove the element 
                window.URL.revokeObjectURL(href); //release the object  of the blob
            });
        }
    },

    mounted() {
    }
}
</script>

