<template>

  <body class="login">
    <div class="container sm:px-10">
      <div class="block xl:grid grid-cols-2 gap-4">
        <!-- BEGIN: Login Info -->
        <div class="hidden xl:flex flex-col min-h-screen">

          <div class="my-auto">
            <img alt="Tinker Tailwind HTML Admin Template" class="-intro-x w-1/2 -mt-16 ml-3" src="/logo.jpg"
              style="border-radius:5px;">
            <div class="-intro-x text-white font-medium text-4xl leading-tight mt-10">

            </div>
            <div class="-intro-x mt-5 text-lg text-white text-opacity-70 dark:text-gray-500">

            </div>
          </div>
        </div>
        <!-- END: Login Info -->
        <!-- BEGIN: Login Form -->
        <div class="h-screen xl:h-auto flex py-5 xl:py-0 my-10 xl:my-0">
          <div
            class="my-auto mx-auto xl:ml-20 bg-white dark:bg-dark-1 xl:bg-transparent px-5 sm:px-8 py-8 xl:p-0 rounded-md shadow-md xl:shadow-none w-full sm:w-3/4 lg:w-2/4 xl:w-auto"
            style="margin:auto;">

            <h2 class="intro-x font-bold text-2xl xl:text-3xl text-center xl:text-left"
              style="color:#FFF; text-align:center;">
              เข้าสู่ระบบ
            </h2>

            <form @submit.prevent="onSubmit">
              <div class="intro-x mt-8">
                <input type="text" class="intro-x login__input form-control py-3 px-4 border-gray-300 block"
                  style="width:100%;" v-model="username" placeholder="ชื่อผู้ใช้">
                <div class="input-group mt-2">
                  <!-- <input type="password" id="password" class="intro-x login__input form-control py-3 px-4 border-gray-300 block mt-4" v-model="password" placeholder="รหัสผ่าน"> -->
                  <input type="password" id="password"
                    class="intro-x login__input form-control py-3 px-4 border-gray-300 block" v-model="password"
                    placeholder="รหัสผ่าน">
                  <div id="input-group-password" class="input-group-text" @click="switchVisibility()"
                    style="width: 58px;padding-top: 11px;">{{ this.switchText }}</div>
                </div>
              </div>
              <div class="intro-x mt-5 xl:mt-8 text-center xl:text-left" style="text-align:center;">
                <!-- <router-link to="/index" active-class="active"> -->
                <button class="btn btn-primary py-3 px-4 w-full xl:w-32 xl:mr-3 align-top"
                  style="color:#00379A; background-color:#FFF;" @click="submitLogin()">เข้าสู่ระบบ </button>
                <!-- </router-link> -->
              </div>
            </form>
          </div>
        </div>
        <!-- END: Login Form -->
      </div>
    </div>


  </body>
</template>

<script>
import AuthenService from '@/services/auth.service';
import { useToast } from "vue-toastification";
export default {
  name: "LoginPage",
  data() {
    return {
      username: "",
      password: "",
      switchText: "Show",
      toast: useToast(),
    };
  },
  props: {},
  methods: {
    submitLogin() {
      this.$store.dispatch('main/changeLoading', true);
      
      if(parseInt(process.env.VUE_APP_PROD_MODE) == 0){
        var dataReq = {
          username: this.username,//adminDkLv2//adminCttCrm
          password: this.password,//1234/Py1234
          
        };
      }
      else{
        var dataReq = {
          username: this.username,
          password: this.password,
        };
      }
      AuthenService.adminLogin(dataReq).then((response) => {
        if (response.status == true) {
          this.toast.success("Logged in successfully", { timeout: 2000 });
            this.$store.dispatch("auth/loginSuccess", response.data).then(() => {
              this.axios.defaults.headers.common['Authorization'] = this.globalConst.authHeader();
              this.$store.dispatch('main/changeLoading', false);
              this.$router.push('/');
            });
        }
        else {
          this.toast.error(response.data, { timeout: 2000 });
          this.$store.dispatch('main/changeLoading', false);
        }
      });
    },
    switchVisibility() {
      this.switchText = (this.switchText == "Show") ? "Hide" : "Show";
      const passwordField = document.querySelector('#password')
      if (passwordField.getAttribute('type') === 'password') passwordField.setAttribute('type', 'text')
      else passwordField.setAttribute('type', 'password')
    }
  },
  components: {},
  mounted() {
    this.$store.dispatch('main/changeLoading', true);
  },
};
</script>
<style scoped>
#input-group-password:hover {
  cursor: pointer;
}
</style>
