<template>
  <div class="intro-y box p-5 mt-5">
    <div class=" flex-col sm:flex-row sm:items-end xl:items-start">
      <form id="tabulator-html-filter-form" class="xl:flex sm:mr-auto"></form>
      <div class=" mt-5 sm:mt-0">
        <div class="body-wrapper">
          <form id="tabulator-html-filter-form" class="xl:flex sm:mr-auto" style="margin-bottom: 1rem;"></form>
        </div>
      </div>
    </div>
    <div class="col-lg-3 col-md-4 col-sm-4 col-xs-12">
      <label style="font-size:24px;">Level</label>

    </div>
    <div class="col-lg-9 col-sm-8 col-md-8 col-xs-12" align="right">
      <button class="w-1/2 sm:w-auto mr-2 mb-3 btn btn-linkedin" @click="exportData()" style="width: auto;">
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
          stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"
          class="feather feather-file-text hidden sm:block w-4 h-4 mr-2">
          <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z"></path>
          <polyline points="14 2 14 8 20 8"></polyline>
          <line x1="16" y1="13" x2="8" y2="13"></line>
          <line x1="16" y1="17" x2="8" y2="17"></line>
          <polyline points="10 9 9 9 8 9"></polyline>
        </svg>
        Download Excel </button>
    </div>
    <div id="chats" class="tab-pane active mr-1 ml-1" role="tabpanel" aria-labelledby="chats-tab">
      <div class="overflow-x-auto scrollbar-hidden">

        <div class="overflow-x-auto">
          <table class="table mt-3" id="table_id">
            <thead style="back">
              <tr class="bg-gray-200 text-center">

                <th class="whitespace-nowrap" style="text-align:center;">Guid</th>
                <th class="whitespace-nowrap" style="text-align:center;">Name</th>
                <th class="whitespace-nowrap" style="text-align:center;">Grade</th>
                <th class="whitespace-nowrap" style="text-align:center;">BeginAmt</th>
                <th class="whitespace-nowrap" style="text-align:center;">EndAmt</th>
                <th class="whitespace-nowrap" style="text-align:center;">Status</th>

              </tr>
            </thead>
            <tbody class=" text-center ">
              <tr v-for="ms in allms" :key="ms.id">

                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ ms.levelGuid }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ ms.levelName }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ ms.levelGrade }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ ms.levelBeginAmt }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ ms.levelEndAmt }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ ms.levelStatus }}</td>
                
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

  </div>

</template>
<script>
import MasterDataService from '@/services/masterdata.service';
import moment from 'moment'
import axios from "axios";
export default {
  name: 'LevelSearch',
  data() {
    return {
      allms: [],
    }
  },
  props: {
  },
  methods: {
    exportData() {
      axios({
        url: process.env.VUE_APP_BASE_API + '/MasterData/Level_Download', // File URL Goes Here
        method: 'GET',
        responseType: 'blob',
      }).then((response) => {
        var blob = new Blob([response.data]);
        var downloadElement = document.createElement("a");
        var href = window.URL.createObjectURL(blob); //create the download url
        downloadElement.href = href;
        downloadElement.download = "LevelMasterdata" + moment(new Date()).format('(DD-MM-YYYY hh.mm น.)') + ".xlsx"; //the name of the downloaded file
        document.body.appendChild(downloadElement);
        downloadElement.click(); //click to file
        document.body.removeChild(downloadElement); //remove the element 
        window.URL.revokeObjectURL(href); //release the object  of the blob
      });
    },
  },
  mounted() {
    this.$store.dispatch('main/changeLoading', true);
    MasterDataService.getLevelMs().then((data) => {
      if (data.status == true) {
        this.allms = data.data;
        $.extend(true, $.fn.dataTable.defaults, {
          "language": {
            "sProcessing": "กำลังดำเนินการ...",
            "sLengthMenu": "แสดง _MENU_ แถว",
            "sZeroRecords": "ไม่พบข้อมูล",
            "sInfo": "แสดง _START_ ถึง _END_ จาก _TOTAL_ แถว",
            "sInfoEmpty": "แสดง 0 ถึง 0 จาก 0 แถว",
            "sInfoFiltered": "(กรองข้อมูล _MAX_ ทุกแถว)",
            "sInfoPostFix": "",
            "sSearch": "ค้นหา:",
            "sUrl": "",
            "oPaginate": {
              "sFirst": "เริ่มต้น",
              "sPrevious": "ก่อนหน้า",
              "sNext": "ถัดไป",
              "sLast": "สุดท้าย"
            }
          }
        });
        $(document).ready(function () {
          $('#table_id').DataTable({
            order: [[2, 'desc']],
          });
        });

        this.$store.dispatch('main/changeLoading', false);
      }
      else {
        $.extend(true, $.fn.dataTable.defaults, {
          "language": {
            "sProcessing": "กำลังดำเนินการ...",
            "sLengthMenu": "แสดง _MENU_ แถว",
            "sZeroRecords": "ไม่พบข้อมูล",
            "sInfo": "แสดง _START_ ถึง _END_ จาก _TOTAL_ แถว",
            "sInfoEmpty": "แสดง 0 ถึง 0 จาก 0 แถว",
            "sInfoFiltered": "(กรองข้อมูล _MAX_ ทุกแถว)",
            "sInfoPostFix": "",
            "sSearch": "ค้นหา:",
            "sUrl": "",
            "oPaginate": {
              "sFirst": "เริ่มต้น",
              "sPrevious": "ก่อนหน้า",
              "sNext": "ถัดไป",
              "sLast": "สุดท้าย"
            }
          }
        });
        $(document).ready(function () {
          $('#table_id').DataTable();
        });
      }
    });
  },
  computed: {}
}
</script>

