<script>
import CustomerService from '@/services/customer.service';
export default {
  name: 'CustomerDetail',
  data() {
    return {
        memberDetail: {
            lstMemberDetail: [
                {
                    memDetTMSID:"",
                    tmsCurrentAddress:"",
                    tmsPostCode:"",
                }
            ]
        },
        benefitList: [],
        ticketAff: {
            lstActCoupon: [],
            lstInaCoupon: [],
        },
        ticketCrm: {
            lstActCoupon: [],
            lstInaCoupon: [],
        },
        memberHistory: {},
        histype: 1,

        objectHistory: {
            lstMemberRemainPoint: [],
            lstMemberRedeem: [],
            lstMemberReward: [],
            lstMemberHistoryPoint: [],
        },
        crmHistory: {},
        affHistory: {},
    }
  },
  props: {
  },
  methods: {
    async initState() {
        this.$store.dispatch('main/changeLoading', true);
        
        await this.getMemberProfile(this.$route.query.memberGuid);
        await this.getBenefitDescription(this.$route.query.memberGuid);
        await this.getAllCoupon(this.$route.query.memberGuid);
        await this.getMemberHistory(this.$route.query.memberGuid);

        this.$store.dispatch('main/changeLoading', false);
    },
    async getMemberProfile(memberGuid) {
      await CustomerService.getMemberProfile(memberGuid).then((res) => {
        this.memberDetail = res.data;
      });
    },
    async getBenefitDescription(memberGuid) {
      await CustomerService.getBenefitDescription(memberGuid).then((res) => {
        this.benefitList = res.data.lstBenefitDetail;
      });
    },
    async getAllCoupon(memberGuid) {
      await CustomerService.getAllCoupon(memberGuid).then((res) => {
        this.ticketCrm = res.data.crm;
        this.ticketAff = res.data.aff;
      });
    },
    async getMemberHistory(memberGuid) {
      await CustomerService.getMemberHistory("CRM", memberGuid).then((res) => {
        if (res.status == true) {
            this.crmHistory = res.data;
            
            this.objectHistory.lstMemberRemainPoint = this.crmHistory?.lstMemberRemainPoint;
            this.objectHistory.lstMemberRedeem = this.crmHistory?.lstMemberRedeem;
            this.objectHistory.lstMemberReward = this.crmHistory?.lstMemberReward;
            this.objectHistory.lstMemberHistoryPoint = this.crmHistory?.lstMemberHistoryPoint;
        }
      });
      await CustomerService.getMemberHistory("AFF", memberGuid).then((res) => {
        if (res.status == true) {
            this.affHistory = res.data;
        }
      });
    },
    switchHistorySelect(event){
        this.histype = event.target.value;
    },
    changeHistoryTab(type){
        this.objectHistory = (type == "CRM") ? this.crmHistory : this.affHistory;
    },
    imageLoadError(event){
        event.target.src = this.$assets.image.noImage;
    }
  },
  async mounted() {
    await this.initState();
  },
  computed: {}
}
</script>
<style scoped>
.benefit-item {
  display: flex;
  background-color: #fff; }
  .benefit-item + .benefit-item {
    border-top: 1px solid #eee; }
  .benefit-item > * {
    flex: 1;
    padding: 8px 15px;
    text-align: left; }
  .benefit-item .value {
    border-left: 1px solid #eee; }
</style>

