<template>
  <div class="intro-y box p-5 mt-5">
    <div class="flex-col sm:flex-row sm:items-end xl:items-start">
      <form id="tabulator-html-filter-form" class="xl:flex sm:mr-auto"></form>
      <div class=" mt-5 sm:mt-0">
        <div class="body-wrapper">
          <form id="tabulator-html-filter-form" class="xl:flex sm:mr-auto" style="margin-bottom: 1rem;"></form>
        </div>
      </div>
    </div>
    <div class="col-lg-3 col-md-4 col-sm-4 col-xs-12 mb-4">
      <label style="font-size:24px;">Customer</label>
    </div>

    <div class="grid grid-cols-12 gap-4">
      <div class="col-start-2 col-span-3 ...">
        <div class="w-full md:w-1/2 px-3 mb-6 md:mb-0">
          <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-first-name">
            ชื่อ
          </label>
          <input class="appearance-none block w-full text-gray-700 border border-red-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white" 
          id="grid-first-name" type="text" placeholder="ชื่อ" v-model="filter.name">
        </div>
      </div>
      <div class="col-start-2 col-span-3 ...">
        <div class="w-full md:w-1/2 px-3 mb-6 md:mb-0">
          <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-first-name">
            E-mail
          </label>
          <input class="appearance-none block w-full text-gray-700 border border-red-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white" 
          id="grid-first-name" type="text" placeholder="E-mail" v-model="filter.email">
        </div>
      </div>
      <div class="col-start-2 col-span-3 ...">
        <div class="w-full md:w-1/2 px-3 mb-6 md:mb-0">
          <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-first-name">
            เบอร์โทรศัพท์
          </label>
          <input class="appearance-none block w-full text-gray-700 border border-red-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white" 
          id="grid-first-name" type="text" placeholder="เบอร์โทรศัพท์" v-model="filter.tel" 
          maxlength="10" @keypress="globalConst.onlyNumber">
        </div>
      </div>
    </div>


    <div class="grid grid-cols-4 gap-4">
        <button class="btn btn-primary" style="width: 22%; margin-left: 1%;" @click="onSearch()">ค้นหา</button>
    </div>


    <div class="tab-pane active mr-1 ml-1" role="tabpanel">
      <div class="overflow-x-auto scrollbar-hidden">

        <div class="overflow-x-auto">
          <table class="table mt-3" id="table_id">
            <thead style="back">
              <tr class="bg-gray-200 text-center">
                <th class="whitespace-nowrap" style="text-align:center;">จัดการ</th>
                <th class="whitespace-nowrap" style="text-align:center;">Level</th>
                <th class="whitespace-nowrap" style="text-align:center;">ชื่อ</th>
                <th class="whitespace-nowrap" style="text-align:center;">นามสกุล</th>
                <th class="whitespace-nowrap" style="text-align:center;">เพศ</th>
                <th class="whitespace-nowrap" style="text-align:center;">เบอร์โทรศัพท์</th>
                <th class="whitespace-nowrap" style="text-align:center;">เบอร์โทรศัพท์<br>(เพื่อนที่แนะนำ)</th>
                <th class="whitespace-nowrap" style="text-align:center;">E-mail</th>
                <th class="whitespace-nowrap" style="text-align:center;">คะแนน CRM</th>
                <th class="whitespace-nowrap" style="text-align:center;">คะแนน Affiliate</th>
                <th class="whitespace-nowrap" style="text-align:center;">จำนวน Affiliate (คน)</th>
              </tr>
            </thead>
            <tbody class=" text-center ">
              <tr v-for="item in customerDal.lstMember" :key="item.memberGuid">
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">
                  <router-link :to="`./customer-detail?memberGuid=${item.memberGuid}`">
                    <button class="btn btn-primary">รายละเอียด</button>
                  </router-link>
                </td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ item.levelName }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ item.memberFirstName }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ item.memberLastName }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ item.memberGenderName }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ item.memberTel }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ item.refTelNo }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ item.memberEmail }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ globalConst.addCurrency(item.memberTotalPoint) }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ globalConst.addCurrency(item.memberTotalAffPoint) }}</td>
                <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ globalConst.addCurrency(item.sumAffiliate) }}</td>
              </tr>
            </tbody>
          </table>
        </div>

        <div class="px-lg-5" v-if="totalPages > 1">
          <div class="float-right">
            <paginate
              v-model="currentPage"
              :page-count="totalPages"
              :page-range="3"
              :margin-pages="2"
              :click-handler="clickCallback"
              :prev-text="'Prev'"
              :next-text="'Next'"
              :container-class="'pagination-cu'"
              :page-class="'page-item'"
            >
            </paginate>
          </div>
        </div>
          
      </div>
    </div>

  </div>
</template>
<script>
import Paginate from 'vuejs-paginate-next';
import CustomerService from '@/services/customer.service';
export default {
  name: 'CustomerList',
  data() {
    return {
      currentPage: 1,
      totalCount: 0,
      totalPages: 0,
      limit: 10,
      offet: 0,

      customerDal: {
        lstMember: [],
        totalCount: 0,
      },
      filter: {
        name: "",
        email: "",
        tel: "",
        // levelGuid: "",
      }
    }
  },
  props: {
  },
  methods: {
    async initState() {
      this.$store.dispatch("main/changeLoading", true);
      await this.getListMember({offset: 0,limit: this.limit});
      this.customerDal.lstMember = await this.mapPropCustList(this.customerDal);
      this.$store.dispatch("main/changeLoading", false);
    },
    async getListMember(payload) {
      // let payload = {
      //   offset: 800,
      //   limit: 20,
      //   // name: "string",
      //   // email: "string",
      //   // tel: "string",
      //   // levelGuid: "3fa85f64-5717-4562-b3fc-2c963f66afa6"
      // };
      await CustomerService.getListMember(payload).then((res) => {
        if(res.status) {
          this.customerDal = res.data;
          if(res.data.totalCount > 0){
            this.totalCount = res.data.totalCount;
            this.totalPages = Math.ceil(this.totalCount/this.limit);
          }
          else{
            this.totalCount = 0;
            this.totalPages = 0;
          }
        }
      });
    },
    async mapPropCustList(custDetail){
      return custDetail.lstMember.map(element => {
        if(element.memberGender == "M"){
          element.memberGenderName = "ชาย";
        }
        else if(element.memberGender == "F"){
          element.memberGenderName = "หญิง";
        }
        else{
          element.memberGenderName = "อื่นๆ";
        }
        return element;
      });
    },
    async clickCallback(pageNum){
      this.$store.dispatch("main/changeLoading", true);
      pageNum = pageNum-1;
      let offset = pageNum*this.limit;
      // await this.getListMember({offset: offset,limit: this.limit});

      let payload = {
        offset: offset,
        limit: this.limit,
        name: this.filter.name.trim(),
        email: this.filter.email.trim(),
        tel: this.filter.tel.trim(),
      };

      await this.getListMember(payload);

      this.$store.dispatch("main/changeLoading", false);
    },
    async onSearch(){
      let payload = {
        offset: 0,
        limit: this.limit,
        name: this.filter.name.trim(),
        email: this.filter.email.trim(),
        tel: this.filter.tel.trim(),
      };

      this.$store.dispatch("main/changeLoading", true);
      console.log("payload",payload);
      await this.getListMember(payload);
      this.customerDal.lstMember = await this.mapPropCustList(this.customerDal);
      this.$store.dispatch("main/changeLoading", false);
    }
  },
  components: {
    paginate: Paginate,
  },
  async mounted() {
    await this.initState();
  },
  computed: {}
}
</script>

<style>
.pagination-cu {
  display: inline-block;
  padding-left: 0;
  margin: 20px 0;
  border-radius: 4px;
}
.pagination-cu > li {
  display: inline;
}
.pagination-cu > li > a,
.pagination-cu > li > span {
  position: relative;
  float: left;
  padding: 6px 12px;
  margin-left: -1px;
  line-height: 1.42857143;
  color: #337ab7;
  text-decoration: none;
  background-color: #fff;
  border: 1px solid #ddd;
}
.pagination-cu > li:first-child > a,
.pagination-cu > li:first-child > span {
  margin-left: 0;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.pagination-cu > li:last-child > a,
.pagination-cu > li:last-child > span {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
.pagination-cu > li > a:hover,
.pagination-cu > li > span:hover,
.pagination-cu > li > a:focus,
.pagination-cu > li > span:focus {
  z-index: 3;
  color: #23527c;
  background-color: #eee;
  border-color: #ddd;
}
.pagination-cu > .active > a,
.pagination-cu > .active > span,
.pagination-cu > .active > a:hover,
.pagination-cu > .active > span:hover,
.pagination-cu > .active > a:focus,
.pagination-cu > .active > span:focus {
  z-index: 2;
  color: #fff;
  cursor: default;
  background-color: #337ab7;
  border-color: #337ab7;
}
.pagination-cu > .disabled > span,
.pagination-cu > .disabled > span:hover,
.pagination-cu > .disabled > span:focus,
.pagination-cu > .disabled > a,
.pagination-cu > .disabled > a:hover,
.pagination-cu > .disabled > a:focus {
  color: #777;
  cursor: not-allowed;
  background-color: #fff;
  border-color: #ddd;
}
.pagination-cu-lg > li > a,
.pagination-cu-lg > li > span {
  padding: 10px 16px;
  font-size: 18px;
  line-height: 1.3333333;
}
.pagination-cu-lg > li:first-child > a,
.pagination-cu-lg > li:first-child > span {
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}
.pagination-cu-lg > li:last-child > a,
.pagination-cu-lg > li:last-child > span {
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}
.pagination-cu-sm > li > a,
.pagination-cu-sm > li > span {
  padding: 5px 10px;
  font-size: 12px;
  line-height: 1.5;
}
.pagination-cu-sm > li:first-child > a,
.pagination-cu-sm > li:first-child > span {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
.pagination-cu-sm > li:last-child > a,
.pagination-cu-sm > li:last-child > span {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}




li.page-item :hover{
  cursor: pointer;
}
.page-item.active .page-link {
    z-index: 3;
    color: #fefefe;
    background-color: #00379A;
    border-color: #00379A;
}
.page-item a {
  color: #00379A;
}
.page-item a:hover {
  color: #00379A;
}
.page-link:focus {
    z-index: 3;
    outline: 0;
    box-shadow: 0 0 0 0.2rem #00369a82;
}
</style>