<template>
  <div class="intro-y box p-5 mt-5">
    <div class=" flex-col sm:flex-row sm:items-end xl:items-start">
      <form id="tabulator-html-filter-form" class="xl:flex sm:mr-auto" ></form>
      <div class=" mt-5 sm:mt-0">
        <div class="body-wrapper">
          <form id="tabulator-html-filter-form" class="xl:flex sm:mr-auto" style="margin-bottom: 1rem;"></form>
        </div>                      
        </div> 
    </div> 
    <div class="col-lg-3 col-md-4 col-sm-4 col-xs-12">
        <label style="font-size:24px;">Banner</label>
    </div>
    <div class="col-lg-9 col-sm-8 col-md-8 col-xs-12" align="right">
        <button  class="w-1/2 sm:w-auto mr-2 mb-3" :class="canMasterBannerDownload ? 'btn btn-linkedin' : 'btn btn-linkedin'" :disabled="canMasterBannerDownload == false" @click="ExportData()" style="width: auto;">
           <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" class="feather feather-file-text hidden sm:block w-4 h-4 mr-2"><path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z"></path><polyline points="14 2 14 8 20 8"></polyline><line x1="16" y1="13" x2="8" y2="13"></line><line x1="16" y1="17" x2="8" y2="17"></line><polyline points="10 9 9 9 8 9"></polyline></svg>
             Download Excel 
        </button>        
    </div>
    
    <div id="chats" class="tab-pane active mr-1 ml-1" role="tabpanel" aria-labelledby="chats-tab" v-if="close">
        <div class="overflow-x-auto scrollbar-hidden">
        <div class="overflow-x-auto">
            <table class="table mt-3" id="table_id">
                <thead style="back">
                    <tr class="bg-gray-200   text-center">
                        
                        <th class="whitespace-nowrap" style="text-align:center;"></th>
                        <th class="whitespace-nowrap" style="text-align:center;">Guid</th>
                        <th class="whitespace-nowrap" style="text-align:center;">Img</th>
                        <th class="whitespace-nowrap" style="text-align:center;">Seq</th>
                        <th class="whitespace-nowrap" style="text-align:center;">FromDateStr (yyyy-MM-dd hh:mm:ss)</th>
                        <th class="whitespace-nowrap" style="text-align:center;">ToDateStr (yyyy-MM-dd hh:mm:ss)</th>
                        <th class="whitespace-nowrap" style="text-align:center;">Status</th>
                        <th class="whitespace-nowrap" style="text-align:center;">Remark</th>

                    </tr>
                </thead>
                <tbody class=" text-center ">
                    <tr v-for="ms in allms" :key="ms.id">
                        <td class="border-b dark:border-dark-5" >
                            <button :class="canMasterBannerDelete ? 'btn btn-danger' : 'btn btn-danger'" :disabled="canMasterBannerDelete == false" @click="showAlert(ms.bannerGuid)" data-toggle="modal" href="#confirm-update" type="submit"> 
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" class="feather feather-trash-2 w-4 h-4 mr-1"><polyline points="3 6 5 6 21 6"></polyline><path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path><line x1="10" y1="11" x2="10" y2="17"></line><line x1="14" y1="11" x2="14" y2="17"></line></svg>
                                ลบ
                            </button>
                        </td>
                        <td class="border-b dark:border-dark-5" >{{ms.bannerGuid}}</td>

                        <td class="border-b dark:border-dark-5" >
                            <img :src="ms.bannerImg_disp" @error="imageUrlAlt" style="margin: auto; width: 50px; height: 50px;"  @click="openImg(ms.bannerImg_disp)"><img />
                            
                        </td>
                        <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ms.bannerSeq}}</td>
                        <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ms.bannerFromDateStr}}</td>
                        <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ms.bannerToDateStr}}</td>
                        <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ms.bannerStatus}}</td>
                        <td class="border-b dark:border-dark-5" style="white-space: nowrap">{{ms.bannerRemark}}</td>
                        
                    </tr>
                    
                </tbody>
            </table>
        </div>
        </div>
    </div>
  </div>
</template>
<script>
import MasterDataService from '@/services/masterdata.service';
import FilterService from '@/services/filter.service';
import { useToast } from "vue-toastification";
import moment from 'moment'
import axios from "axios";
import globalConst from "@/utils/global-const.js";
export default {
  name: 'BannerSearch',
  data() {
    return {
      toast: useToast(),
      isReceiptImageError:false,
      imageDefault:"dist/images/image-not-found.png",
      userId: 0,
      body: "",
      baseApi: process.env.VUE_APP_BASE_API,
      allms:[],
      detalfilter:[],
      recReceiptNo:"",
      recCreateDateFrom:"",
      recCreateDateTo:"",
      storeName:"",
      custFullName:"",
      custTel:"",
      recStatus:"",
      data:[],
      show:false,
      close:true,
    }
  },
  props: {
  },
  methods:{
    ...MasterDataService,

    ...FilterService,
    showAlert(data) {
      
    this.$swal.fire({
            title: 'ยืนยันการลบข้อมูล?',
            showDenyButton: true,
            confirmButtonText: 'ยืนยัน',
            denyButtonText: 'ยกเลิก',
            }).then((result) => {
            /* Read more about isConfirmed, isDenied below */
            if (result.isConfirmed) {
                this.$store.dispatch('main/changeLoading',true);
                this.$swal.fire(this.Delete(data))
            }
        })
    },
    Delete(data){
    MasterDataService.Delete(data).then((data) => {
      if(data.status == true){
        location.reload();
      }
      else {
        this.toast.error(data.data, {timeout: 2000});
      }
    })
    },
    openImg(data){
        let imageOpenPreview = (this.isReceiptImageError) ? this.imageDefault : data;
        window.open(imageOpenPreview,'targetWindow', 'toolbar=no, location=no, status=no, menubar=no, scrollbars=yes, resizable=yes, width=1090px, height=550px, top=25px left=120px');
    },
    replaceImgByDefault(e){
        this.isReceiptImageError = true;
        e.target.src = this.imageDefault;
    },
    ExportData(){
     axios({
                url: process.env.VUE_APP_BASE_API+'/MasterData/Banner_Download', // File URL Goes Here
                method: 'GET',
                responseType: 'blob',
            }).then((response) => {
                var blob = new Blob([response.data]);
                var downloadElement = document.createElement("a");
                var href = window.URL.createObjectURL(blob); //create the download url
                downloadElement.href = href;
                downloadElement.download = "BannerMasterdata"+moment(new Date()).format('(DD-MM-YYYY hh.mm น.)')+".xlsx"; //the name of the downloaded file
                document.body.appendChild(downloadElement);
                downloadElement.click(); //click to file
                document.body.removeChild(downloadElement); //remove the element 
                window.URL.revokeObjectURL(href); //release the object  of the blob
            });
    },
    tocur(amount) {
      return amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },

  },
  
  async mounted() {
    this.$store.dispatch('main/changeLoading',true);
    try {
      await MasterDataService.getBannerMs().then((data) => {
        if(data.status == true){
          this.allms = data.data;
          $.extend(true, $.fn.dataTable.defaults, {
            "language": {
              "sProcessing": "กำลังดำเนินการ...",
              "sLengthMenu": "แสดง _MENU_ แถว",
              "sZeroRecords": "ไม่พบข้อมูล",
              "sInfo": "แสดง _START_ ถึง _END_ จาก _TOTAL_ แถว",
              "sInfoEmpty": "แสดง 0 ถึง 0 จาก 0 แถว",
              "sInfoFiltered": "(กรองข้อมูล _MAX_ ทุกแถว)",
              "sInfoPostFix": "",
              "sSearch": "ค้นหา:",
              "sUrl": "",
              "oPaginate": {
                "sFirst": "เริ่มต้น",
                "sPrevious": "ก่อนหน้า",
                "sNext": "ถัดไป",
                "sLast": "สุดท้าย"
            }
          }
        });
          $(document).ready( function () {
              $('#table_id').DataTable({
                  order: [[2, 'desc']],
              });
          });
        }
        else {
          this.allrec = "ไม่มีข้อมูล";
          $.extend(true, $.fn.dataTable.defaults, {
              "language": {
                      "sProcessing": "กำลังดำเนินการ...",
                      "sLengthMenu": "แสดง _MENU_ แถว",
                      "sZeroRecords": "ไม่พบข้อมูล",
                      "sInfo": "แสดง _START_ ถึง _END_ จาก _TOTAL_ แถว",
                      "sInfoEmpty": "แสดง 0 ถึง 0 จาก 0 แถว",
                      "sInfoFiltered": "(กรองข้อมูล _MAX_ ทุกแถว)",
                      "sInfoPostFix": "",
                      "sSearch": "ค้นหา:",
                      "sUrl": "",
                      "oPaginate": {
                                      "sFirst": "เริ่มต้น",
                                      "sPrevious": "ก่อนหน้า",
                                      "sNext": "ถัดไป",
                                      "sLast": "สุดท้าย"
                      }
              }
          });
          $(document).ready( function () {
              $('#table_id').DataTable();
          });   
        }
      });
    }
    catch (err) {
      this.toast.error(err.data, {timeout: 2000});
    }
    this.$store.dispatch('main/changeLoading',false);
    
  },
  computed: {}
}
</script>

